.main-container {
    display: grid;
    width: 100%;
    margin:0;
    padding: 0;
    max-width: 1700px;
    min-width: 900px;
    grid-template-columns: .5fr 1.5fr 1fr .5fr;
    grid-template-rows: repeat(auto);
    grid-template-areas:
        "nav nav nav nav"
        "yourPan photo instructions vars"
        "yourPan ingred instructions vars"
        "yourPan ingred instructions vars"
        "yourPan ingred instructions vars"
        "yourPan ingred instructions vars"
        "footer footer footer footer";
    grid-gap: 0.2rem;
}
.nav-bar {
    grid-area: nav;
    background-color: rgb(101, 165, 165);
}

.main-photo {
    grid-area: photo;
    background-color: rgb(198, 255, 255);
}

.ingredients {
    grid-area: ingred;
    background-color: rgb(198, 255, 255);
}

.your-pantry {
    grid-area: yourPan;
    background-color: rgb(198, 255, 255);
}

.directions {
    grid-area: instructions;
    background-color: rgb(198, 255, 255);
}

.variations {
    grid-area: vars;
    background-color:rgb(198, 255, 255);
}

.footer {
    grid-area: footer;
    background-color: rgb(245, 245, 245);
}
/* navigation styles start here */
.header {
  height: 3.4em;

}
.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-list {
  display: flex;
  list-style: none;
  justify-content: center;
}

li, a, a:active, a:visited {
  color: #fff;
  margin: .5em 2em;
  text-decoration: none;
  align-items: center;
  align-self: center;
}

.nav-list-search {
  display: flex;
  list-style: none;
  justify-content: flex-end;
}
.nav-list-search>li {
  margin: .4em;
}

@media screen and (max-width: 1000px) {
  .nav {
    display: flex;
    justify-content: center;
    align-items: center;
}
  li, a, a:active, a:visited {
    margin: .5em 1em;
  }
}
